img {
  height: 100%;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  position: relative;
  z-index: -1;
}

.image-container {
  position: relative;
}

.social-media-container {
  width: 100%;
  position: absolute;
  bottom: 10px;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  z-index: 1
}

.hide-social-media-container {
  display: none;
}

@media (max-width: 991px) {
  img {
    padding: 5px;
  }
}
.gif-grid {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  grid-template-rows: 200px 200px 200px 200px;
  margin-left: -5px;
  margin-right: -5px;
}

@media (max-width: 991px) {
  .gif-grid {
    grid-template-columns: 50% 50%;
    grid-template-rows: auto;
  }
}

@media (max-width: 479px) {
  .gif-grid {
    grid-template-columns: 100%;
  }
}
.top-contain {
  padding-top: 50px;
}

.gif-list {
  padding-top: 30px;
}
